import React, { useState } from 'react';
import axios from 'axios';


const DataForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
    num:'',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const { name, email, message,num } = formData;

    // Encode data for URL parameters
    const encodedName = encodeURIComponent(name);
    const encodedEmail = encodeURIComponent(email);
    const encodedMessage = encodeURIComponent(message);
    const encodednum = encodeURIComponent(num);

    const url = `https://script.google.com/macros/s/AKfycbzWlwfrth3IOgb6mgCy_KY8alc8hEUdTb2MYkMztqOoWpm2qYeC1hGjSZYsAU2Fe8zIBw/exec?data1=${encodednum}&data2=${encodedName}&data3=${encodedEmail}&data4=${encodedMessage}&data5=1990-01-01&data6=1234567890&&`;

    try {
      const response = await axios.post(url);
      console.log(response.data);
      // Reset the form
      setFormData({
        name: '',
        email: '',
        message: '',
      });
    } catch (error) {
      console.error('Error submitting data:', error);
    }
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
      <div className="mb-3">
          <input
            className="form-control"
            type="tel"
            name="num"
            value={formData.num}
            onChange={handleChange}
            placeholder="Mobile Number"
            required
          />
        </div>
        <div className="mb-3">
          <input
            className="form-control"
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="Name"
          />
        </div>
        <div className="mb-3">
          <input
            className="form-control"
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            placeholder="Email"
          />
        </div>
        <div className="mb-3">
          <textarea
            className="form-control"
            name="message"
            value={formData.message}
            onChange={handleChange}
            rows="6"
            placeholder="Message"
          />
        </div>
        <div>
          <button className="btn btn-primary shadow d-block w-100" type="submit">
            Send
          </button>
        </div>
      </form>
    </div>
  );
};

export default DataForm;
